import React from "react";
import { BaseComponent, View, Controller } from "../../components";

class App extends BaseComponent {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div
        id="app"
        style={{
          height: "100%",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <div
          style={{
            height: "80%",
            width: "95%",
            backgroundColor: "rgb(3,54,73)",
            alignItems: "center",
            boxShadow:
              "0 0px 0px 0 rgba(0, 0, 0, 0.92),0 4px 8px 0 rgba(0, 0, 0, 0.92)",
          }}
        >
          <View />
        </div>
        <div
          style={{
            height: "10%",
            width: "95%",
            backgroundColor: "rgb(3,101,100)",
            boxShadow:
              "0 4px 8px 0 rgba(0, 0, 0, 0.52),0 4px 8px 0 rgba(0, 0, 0, 0.52)",
          }}
        >
          <Controller />
        </div>
      </div>
    );
  }
}

export default App;



